    --col-heavyDark: #0CABC7;
    --col-bttonAndLink: #43CD43;
    --col-paragraphBg: #F4B41A;
    --col-lightColor: #ABF2D3;
    --col-foreGround: #F1FAFA;
    --col-darkBlue: #143D59;
    --col-browny: #88862B;
    --col-light-bg: rgba(196, 196, 196, 0.21);
