    /**
       * CheckBox
       */
.container-event {
    display: block;
    position: relative;
    padding-top: 3px;
    padding-bottom: 10px;
    padding-left: 25px;
    font-size: 0.9rem;
    color: #000000;
    cursor: pointer;
}

.container-event input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkMark {
    position: absolute;
    border: 1px solid #d9d9d9;
    top: 5px;
    left: 0;
    height: 17px;
    width: 17px;
    background-color: #ffffff;
}

.checkMark:hover {
    border: 1px solid var(--col-heavyDark);
}

.checkMark-gray {
    background-color: #e7e7f3;
}

.container-event:hover input ~ .checkMark {
    background-color: var(--col-heavyDark);
}

.container-event input:checked ~ .checkMark {
    background-color: var(--col-heavyDark);
}

.checkMark:after {
    content: "";
    position: absolute;
    display: none;
}

.container-event input:checked ~ .checkMark:after {
    display: block;
}

.container-event .checkMark:after {
    left: 5px;
    top: 2px;
    width: 3px;
    height: 7px;
    border: solid white;
    border-width: 0 1px 1px 0;
    transform: rotate(35deg);
}

